body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.header-main {
    width: 100%;
    height: 50px;
    background-color: #1f1f1f;
    align-items: center;
    display: flex;
}

.header-title {
    margin-right: auto;
    padding-left: 20px;
    color: white;
}

.header-subtitle {
    font-size: 12px;
    padding-left: 5px;
    color: gray;
}
html, body {
  height: 100%;
}

#root {
  height: 100%;
}

.div-main {
  height: 100%;
}
/* #3e5cf9 */

.main-button {
    width: 200px;
    height: 70px;
    border: 0;
    background-color: #1b1b1b;
    margin: 10px 10px 17px 10px;
    box-shadow: 0 9px #353535;
}

.main-button:active {
    background-color: #2f2f2f;
    -webkit-transform: translateY(4px);
            transform: translateY(4px);
    box-shadow: 0 5px #353535;
}

.main-button:hover {
    background-color: #2f2f2f;
}

.main-button > span {
    color: #ffffff;
    font-size: 20px;
}

.main-button:focus {
    outline: 0;
}

.div-wrapper {
    display: flex;
    flex-direction: column;
}

.div-container {
    justify-content: center;
    align-items: center;
    display: flex;
    width: 100%;
    height: 100vh;
}

.div-container>div {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    z-index: 1;
}

.title-bold {
    color: #ffffff;
    font-size: 50px;
    font-weight: bold;
    margin: 10px;
}

.subtitle {
    font-size: 12px;
    padding-left: 5px;
    color: gray;
}

.android-icon {
    width: 15px;
    vertical-align: middle;
    margin-left: 5px;
}

.first-wrapper::after {
    content:'';
    background-image: url(/static/media/home-bg.d7e67360.jpg);
    width: 100%;
    height: calc(100% - 50px);
    position: absolute;
    background-size: cover;
    -webkit-filter: grayscale(100%);
            filter: grayscale(100%);
    opacity: 0.1;
}

@media only screen and (max-width: 520px) {
    .div-container {
        flex-direction: column;
    }
}
